import VConsole from 'vconsole';
import { IPluginBridge } from '../bridge/plugin/interface';

export function get_custom_vconsole():void {
  const plugin = new VConsole.VConsolePlugin('nemo_custom', 'Nemo 自定义');

  plugin.on('renderTab', (callback:(element:string|Element) => void) => {
    callback('Nemo自定义测试');
  });

  plugin.on('addTopBar', (callback:(btns:TopBarButton[]) => void) => {

    const btn_redirect = {
      name: '跳转到线上页面',
      onClick: async() => {
        window.location.href = 'http://192.168.108.44:4000';
      },
    };

    const btn_init_mock_plugin = {
      name: '初始化demo插件',
      onClick: async() => {
        const bridge:IPluginBridge = (window as any).nemo_plugin_bridge;

        const res = await bridge.init_plugin({
          plugin_name: 'mock_plugin',
          plugin: './plugin.js',
        });

        console.log('init plugin res: ', res);
      },
    };

    callback([
      btn_redirect,
      btn_init_mock_plugin,
    ]);
  });

  const vconsole = new VConsole();

  vconsole.addPlugin(plugin);
}

interface TopBarButton {
  name:string;
  onClick:() => void;
}
